$width: 2.188rem;
$height: 1.25rem;
$padding: 0.188rem;

$switch-active-color: $success;


.switch {
    @include switch();

    &.lg {
        @include switch(100px, 30px);
    }
}

/* Rounded sliders */
.slider.round {
    border-radius: $height;
}
.slider.round:before {
    border-radius: 50%;
}